<template>
    <section class="pa-4 pa-sm-6 pa-md-8">
        <v-responsive max-width="868" class="mx-auto">
            <section id="home">
                <h3 class="text-h4 mb-2"> Estrutura </h3>
                <p class="font-weight-light"> Organização de pastas do código fonte do projeto, para aplicação final e componentes. </p>
                <v-simple-table dense>
                    <template v-slot:default>
                        <thead>
                            <tr>
                                <th class="text-left" style="width: 20%;"> Pasta </th>
                                <th class="text-left" style="width: 80%;"> Descrição </th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(item, index) in getFolders" :key="index">
                                <td class="text-left"> {{ item.folder }} </td>
                                <td class="text-left" v-html="item.description"></td>
                            </tr>
                        </tbody>
                    </template>
                </v-simple-table>

                <h3 class="text-h6 font-weight-regular mt-8 mb-2"> XTPG-UI </h3>
                <v-divider class="mb-4"></v-divider>
                <v-simple-table dense>
                    <template v-slot:default>
                        <thead>
                            <tr>
                                <th class="text-left" style="width: 20%;"> Pasta </th>
                                <th class="text-left" style="width: 80%;"> Descrição </th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(item, index) in getUIFolders" :key="index">
                                <td class="text-left"> {{ item.folder }} </td>
                                <td class="text-left" v-html="item.description"></td>
                            </tr>
                        </tbody>
                    </template>
                </v-simple-table>
            </section>
        </v-responsive>
    </section>
</template>

<style scoped>

</style>

<script>
export default {
    name: 'Structure',
    data() {
        return {}
    },
    computed: {
        getFolders() {
            return this.$store.state['ui'].folders.filter(item => item.folder.indexOf('/src/plugins/xtpgui') <= -1);
        },
        getUIFolders() {
            return this.$store.state['ui'].folders.filter(item => item.folder.indexOf('/src/plugins/xtpgui') > -1);
        }
    },
}
</script>
